.fbi-page {
    font-family: Arial, sans-serif;
    text-align: center;
    padding: 20px;
    background-color: #f8f9fa;
  
    h1 {
      font-size: 2.5rem;
      color: #343a40;
      margin-bottom: 20px;
      text-transform: uppercase;
    }
  
    .container {
      max-width: 1200px;
      margin: 0 auto;
      padding: 20px;
      display: flex;
      flex-wrap: wrap;
      gap: 20px;
      justify-content: center;
  
      ul {
        list-style: none;
        padding: 0;
        margin: 0;
        display: flex;
        flex-wrap: wrap;
        gap: 20px;
  
        li {
          background: #ffffff;
          border: 1px solid #dee2e6;
          border-radius: 10px;
          box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
          padding: 15px;
          width: 300px;
          text-align: left;
          display: flex;
          flex-direction: column;
          align-items: center;
  
          img {
            width: 100%;
            height: 200px;
            object-fit: cover;
            border-radius: 8px;
            margin-bottom: 10px;
          }
  
          h2 {
            font-size: 1.5rem;
            color: #495057;
            margin: 10px 0;
            text-align: center;
          }
  
          p {
            font-size: 0.9rem;
            color: #6c757d;
            margin: 5px 0;
  
            &:first-of-type {
              font-weight: bold;
              color: #343a40;
            }
          }
  
          &:hover {
            box-shadow: 0 8px 10px rgba(0, 0, 0, 0.15);
            transform: translateY(-5px);
            transition: all 0.3s ease-in-out;
          }
        }
      }
    }
  }
  